import React, { useState, useEffect } from "react";
import Swiper from "react-id-swiper";
import brand1 from "../assets/images/brand-1-1.png";
import "swiper/css/swiper.css";
import Number from '../template/Number'

const BrandsTwo = () => {

  const isBrowser = typeof window !== "undefined"
  const today = new Date().getDate()
  const [numbers, setNumbers] = useState([])

  useEffect(() => {
    if(isBrowser) {
      const n = Math.floor(Math.random() * 28);
      fetch(`/numbers/${n}.json`)
      .then(res => res.json())
      .then(setNumbers)
    }
  }, [])



  return (
    <section className="brand-two ">
      <div className="container" id="numbers">
        <div className="block-title">
          <h2 className="block-title__title">Thousands of numbers to choose from.</h2>
        </div>
        <Number
        pageContext={{
          items: numbers

        }}
          
        />
      </div>
    </section>
  );
};

export default BrandsTwo;
